<template>
<main class="main-container">
    <div class="list-view">
        <Article v-for="{ title, sha, date } in filteredList" :key="sha">
            <PostListCard :title="title" :link="'/post/'+sha"
                    :date="date"
            ></PostListCard>
            <hr>
        </Article>
    </div>
</main>
</template>
<script>
import PostListCard from '../components/PostListCard.vue'
export default {
  components:{
      PostListCard,
  },
  computed:{
    filteredList(){
        return this.$store.getters.list
    },
  },
  mounted(){
      // 
      // 1.tags=>tagslist=>
      this.$store.dispatch('getTopicsList').then(list => {
        this.$store.dispatch('updateCacheList', list)
        this.$store.dispatch('updateHomeList', list)
        // 
      })
    }
}
</script>

<style lang="less" scoped>
.main-container{
    position: relative;
    margin: 0 auto;
    width: 100%;
    max-width: 960px;
}
.list-view{
    padding: 20px 50px;
}
</style>



