<template>
  <footer class="footer">
    Copyright  {{ thisYear }} | cosine
  </footer>
</template>

<script>
  export default {
    data () {
      return {
        thisYear: new Date().getFullYear()
      }
    }
  }
</script>
<style lang="less" scoped>
.footer {
    box-sizing: border-box;
    line-height: 3rem;
    height: 3rem;
    text-align:center;
    color: #586069 !important;
    a {
        color: #586069 !important;
        text-decoration:underline
    }
}

</style>




