<template>
  <div class="postCard-box">
        <div class="postCard-main">
            <a :href=link>
                <h2 class="post-title">
                    {{title}}
                </h2>
                <h3 class="post-subtitle">
                    {{subtitle || ''}}
                </h3>
                <p class="post-content-preview">{{des || `desReact NativeAndroidiOS AppReact NativeAndroidiOSReact NativeAndroidiOS
BugsAndroidiOS`}}</p>
            </a>
        </div>
        <ul class="postCard-tags clearfix">
            <li><a>Javascript</a></li>
            <li><a>React.js</a></li>
        </ul>
        <div class="postCard-time">
            <time pubdate="pubdate" :datetime="date | formatDate" :title="date | formatDate" class="item-date">{{ date | timeago }}</time>
        </div>
  </div>
</template>
<script>
export default {
  props:{
      link:String,
      title:String,
      subtitle:String,
      des:String,
      tags:Array,
  },
  data(){
      return {
        date:'2018-02-15',
      }
  }
}
</script>
<style lang="less" scoped>
.postCard-box{
    display: flex;
    flex: 1 1 auto;
    // align-items: center;
    padding: 1rem 2rem;
    // justify-content: center;
    flex-direction: column;
    //border-bottom: 1px #eee solid;
    .postCard-main{
        a{
            // font-size: 1rem;
            // font-weight: 600;
            &:hover{
                color: #4877a5;
            }
            color: inherit;
            .post-title{
                font-size: 26px;
                line-height: 1.3;
                margin-bottom: 10px;
            }
            .post-subtitle{
                font-size: 15px;
                line-height: 1.3;
                margin: 0;
                font-weight: 300;
                margin-bottom: 10px;
            }
            .post-content-preview{
                font-size: 0.9rem;
                font-style: italic;
                color: #a3a3a3;
            }
        }
    }
    .postCard-time{
        .item-date {
            display: inline-block;
            font-size: 0.9rem;
            color: #888;
        }
    }
    .postCard-tags{
        list-style: none;
        margin: 0;
        padding: 5px 0px;
        font-size: 0.9rem;
        li{
            float: left;
            padding-right:10px;
            a{
                text-decoration: none;
                cursor: pointer;
                color: #888;
                &:hover{
                    color: #007fff;
                }
            }
        }
    }
}


</style>





